import { Comic } from "../types";

const getInitial = (comic: Comic) => {
    const initial = comic.howRead[0];
    if ("" <= initial && initial <= "") {
        return "";
    } else if (initial <= "") {
        return "";
    } else if (initial <= "") {
        return "";
    } else if (initial <= "") {
        return "";
    } else if (initial <= "") {
        return "";
    } else if (initial <= "") {
        return "";
    } else if (initial <= "") {
        return "";
    } else if (initial <= "") {
        return "";
    } else if (initial <= "") {
        return "";
    }
    return initial;
}

